//fonts
@mixin font($alias, $name) {
   @font-face {
      font-family: $alias;
      src: url("../fonts/" + $name + ".eot");
      src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".ttf") format("truetype");
      font-weight: normal;
      font-style: normal; } }

@include font('GothamPro-Bold', 'GothamPro-Bold');
@include font('GothamPro-Light', 'GothamPro-Light');
@include font('GothamPro-Medium', 'GothamPro-Medium');
@include font('GothamPro', 'GothamPro');
@include font('Grammatika-Light', 'Grammatika-Light');

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
   $font-family: null;
   @if $name == "gp700" {
      $font-family: "GothamPro-Bold"; }
   @if $name == "gp300" {
      $font-family: "GothamPro-Light"; }
   @if $name == "gp500" {
      $font-family: "GothamPro-Medium"; }
   @if $name == "gp400" {
      $font-family: "GothamPro"; }
   @if $name == "gram" {
      $font-family: "Grammatika-Light"; }
   font-family: $font-family, Arial, sans-serif;
   @if $font-family != null {
      font-weight: normal; }
   @if $font-size != null {
      font-size: $font-size * 1px; }
   @if $line-height != null {
      line-height: $line-height * 1px; }
   @if $letter-spacing != null {
      letter-spacing: ($letter-spacing / 1000) * 1em; } }
